.MiddleNavwrapper {
  @apply flex flex-row justify-center w-full mt-5 items-center;
}

.MiddleNavMain {
  @apply flex flex-row justify-evenly w-full items-center;
}
.MiddleNavLogo {
  @apply w-32;
}
.MiddleNavLogo img {
  @apply w-5;
}

.MiddleNavlinks {
  @apply flex w-2/4 justify-around  text-lg font-bold;
}
.MiddleNavlinks ul li {
  @apply text-3xl font-bold cursor-pointer;
}

.MiddleNavlinks ul li:hover{
    cursor:pointer;
    @apply text-primary-teal/50 ease-in-out duration-150;
}

.navlink{
  @apply cursor-pointer hover:text-primary-teal/50 transition-none;
}
.SubNavOne {
  @apply absolute top-32 left-1/4 w-3/5 h-4/5 transition duration-150 ease-in;
  transition: width 250ms ease, height 250ms ease, opacity 150ms ease,
  transform 200ms ease-in;
  /* transform-origin: 0%;
  transform: translateX(-50%) rotate3d(1, 0, 0, -15deg);
  perspective: 100px;
  transition: width 250ms ease, height 250ms ease, opacity 150ms ease,
    transform 200ms ease-in;
  overflow: hidden;
  opacity: 0;
  pointer-events: none; */
}
.SubNavTip {
  @apply absolute w-5 h-5 top--10 left-12 bg-primary-teal transition duration-150 ease-in-out;
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  transition: transform 100ms linear;
  
}
.SubNavOneWrapper{
    @apply w-full absolute bg-primary-teal h-full rounded-3xl transition-none ;
     transform: translateX(-35%);
  /*transition: transform 250ms linear, opacity 250ms ease-in; */
}
.SubNavOneWrapper:active{
  @apply ease-linear duration-150 animate-bounce;
}
.SubNavOneWrapper{
    @apply flex flex-row justify-around p-10 w-full absolute top-3;
}

.SubNavLinks{
    @apply flex flex-col justify-between items-center;
}
.SubNavLinks h4{
    @apply text-xl text-white font-semibold;
}
.SubNavLink{
    @apply flex ;
}

.SubNavLink a{
    @apply text-white ;
}

.SubNavTwo{
    @apply absolute top-32 left-80 w-2/5 h-4/5 ;
}

.SubNavTwoWrapper{
  @apply flex flex-col justify-between  p-10 w-full h-4/5 absolute top-3 bg-primary-teal rounded-3xl transition-none;
  transform: translateX(-35%);
   
}

.SubNavTwoTip {
    @apply absolute w-5 h-5 top--20 left-48 bg-primary-teal;
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    transition: transform 100ms linear;
    
  }

  .SubNavLinkshead{
    @apply text-lg font-bold text-white;
  }

.SubNavTwoMain{
    @apply flex flex-row justify-between h-3/4;
}

.SubNavThree{
    @apply absolute top-32 left-96 w-2/6 h-3/5;
}
.SubNavThreeWrapper{
  @apply flex flex-col justify-between  p-10 w-full h-4/5 absolute top-3 bg-primary-teal rounded-3xl;
  transform: translateX(-5%);
  transition: transform 250ms linear, opacity 250ms ease; 
}

.SubNavThreeTip {
  @apply absolute w-5 h-5 top--10 left-72 bg-primary-teal;
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  transition: transform 100ms linear;
}
.SubNavFour{
    @apply absolute top-32 left-2/4 w-2/6 h-3/5;
}
.SubNavFourTip {
  @apply absolute w-5 h-5 top--20 left-32 bg-primary-teal;
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  transition: transform 100ms linear;
  
}

.MiddleNavButton{
    @apply w-36 h-10 border-2 rounded-3xl border-primary-teal;
}


.hamburgerMenu{
  display: none;
}

