@media (max-width: 900px) {
  .MiddleNavwrapper{
    @apply justify-between;
  }
  .MiddleNavMain {
    @apply flex flex-row justify-around w-full items-center;
  }
  .MiddleNavlinks{
    display: none;
  }
  .MiddleNavButton{
    display: none;
  }
  .hamburgerMenu{
    @apply w-12 h-12 rounded-full flex flex-col justify-between p-3  bg-primary-dark/50 hover:cursor-pointer;
    
  }
  .hamburgerMenu span{
  @apply w-full h-1 bg-white/90 block mt-0.5;
    
  }

  /*styling responsive nav section*/

  .MiddleNavResponsive{
    @apply absolute top-3 left-6 h-full;
    width: 92vw;
    height: 95vh;
  }

  .responsiveWrapper{
    @apply w-full h-full bg-primary-teal flex flex-col p-5 rounded-3xl  ;
  }

  .NavCancel{
    @apply flex justify-end rounded-full p-3 items-end;
  }

  .NavCancel h3{
    @apply text-white;
  }
  .responsiveNavWrapper{
    @apply flex flex-col w-3/4 h-3/4 justify-between;
  }
  .NavLinks{
    @apply flex flex-row justify-between mb-2;
  }
  .NavLink{
    @apply flex flex-col;
  }
  .responsiveLinks h3{
    @apply text-xl text-white/70 hover:cursor-pointer;
  }
  .removeNav{
    @apply hover:cursor-pointer;
  }
  .NavLink a{
    @apply text-white ;
}
.responsiveNavBottom{
  @apply w-full h-full bg-primary-dark/70 flex justify-evenly items-center rounded-3xl text-xl text-white;
}
.responsiveNavButton{
  @apply w-36 h-10 border-2 rounded-3xl border-white text-white;
}
}
