.mainWrapper{
    @apply w-full flex;
}

.textWrapper{
    @apply flex w-full justify-start px-6 pt-6;
    @apply md:px-8 md:pt-40;
    @apply  lg:py-60;
}

.title{
    @apply w-full text-5xl lg:text-6xl font-bold;
}

.subtitle{
    @apply w-full text-3xl lg:text-4xl font-bold mt-10;
}

.button{
    @apply flex px-6 text-white bg-black text-lg py-2 rounded-full font-semibold mt-8 shadow-md hover:bg-white hover:text-black;
}

.imageWrapper{
    @apply hidden lg:w-1/2 lg:flex justify-end items-center;
}