.universityListingWrapper{
    @apply w-full relative bg-[#012] overflow-auto flex;
}

.universityListingBackground{
    @apply bg-[#04141a] bg-opacity-80 w-full relative;
}

.universityListingBackgroundOpacity{
    @apply bg-[#034] w-full bg-opacity-70;
}

.cardsList{
    @apply w-full py-28  px-4 ;
    @apply md:py-72 md:px-10
}

.tabTitle{
    @apply px-4 py-2 font-semibold text-gray-400 text-base;
}

.tabTitleSelected{
    @apply px-4 py-2 font-semibold text-primary-teal border-b-2 border-b-primary-teal text-base;
}

.regionUniversitiesWrapper{
    @apply w-full mx-auto;
}

.regionUniversitiesMenuDesktop{
    @apply hidden mb-3;
    @apply  md:block;
}

.menuDesktopKeys{
    @apply flex w-full justify-center;
}

.menuMobileKeys{
    @apply flex w-full px-6;
}

.toggleButton{
    @apply w-1/6 flex justify-center items-center;
}


    
.regionUniversitiesMenuMobile{
    @apply block mb-3;
    @apply md:hidden
}



