.containcard {
    @apply flex justify-center mt-10 pt-5;
} 

.divcard {
    @apply rounded-3xl border p-8 shadow-xl w-11/12 bg-primary-white;
}

.headerdiv {
    @apply flex w-full items-center justify-between border-b mb-2;
} 

.fisrtcontent {
    @apply flex items-center space-x-3;
}

.imgstyle{
    @apply h-28 w-28 rounded-full bg-slate-400 mb-4;
}

.textimgstyle{
    @apply mb-2 text-xl font-bold;
}

.divtextstyle{
    @apply mt-3 mb-6;
}

.textstyle{
    @apply text-base text-justify font-sans font-medium;
}