.universityCardWrapper{
    @apply transition mb-5 ease-in-out duration-200 w-72 h-40 bg-white  rounded-2xl pt-6 pl-4 bg-opacity-40 text-white hover:bg-opacity-100 hover:text-black;
    @apply xl:w-[340px];
}

.universityCardWrapperIndexed {
    @apply mb-5 w-72 xl:w-[340px] h-40 bg-white  rounded-2xl pt-6 pl-4 text-black
}

.universityButton{

    @apply font-semibold px-3 py-1 rounded-3xl bg-sky-900 text-white;
}

.universityName {
    @apply text-2xl font-semibold mb-7;
}

/* Wuyika your Tailwind here */


.cardBox {
    @apply w-[23rem] my-4 shadow-lg rounded-bl-3xl rounded-br-3xl
}

.upSection {
    @apply bg-[url('../../resources/images/road.jpeg')] bg-cover bg-center rounded-tl-3xl rounded-tr-3xl py-6 px-6 h-[10.5rem]
}

.downSection {
    @apply px-6 w-full pb-4
}

.upSectionBox {
    @apply px-[10px] py-[4.5px] bg-gray-300 rounded-2xl max-w-fit opacity-90
}

.logoSection {
    @apply text-white flex items-center justify-center w-14 h-14 rounded-full shadow-xl bg-white -mt-8
}

.allText {
    @apply flex flex-col items-start
}

.headText {
    @apply text-sm
}

.title {
    @apply text-black font-semibold
}

.description {
    @apply text-gray-500 mt-1
}

.bottomText {
    @apply text-sm
}

.circleLogo {
    @apply bg-primary-teal flex items-center justify-center w-8 h-8 rounded-full
}

.circle {
    @apply bg-gray-200 flex items-center justify-center w-8 h-8 rounded-full
}

.advantagesWrapper{
    @apply w-full rounded-3xl px-3 py-4 text-white font-bold;
    @apply  md:px-10 md:py-12;
}

.advantagesTitle{
    @apply text-4xl;
    @apply md:text-5xl;
    @apply lg:text-6xl; 
}

.advantagesCheck{
    @apply text-primary-teal mt-2 mr-3;
}

.advantagesButton{
    @apply transition ease-out duration-300 border-2 border-white mb-6 py-1 px-3 text-white rounded-3xl mt-12 bg-transparent hover:text-black hover:bg-white hover:font-semibold;
}


.guarantyWrapper{
    @apply p-8 h-[400px] rounded-xl shadow-xl;
}

.guarantyTitle{
    @apply mt-3 pr-10 font-bold text-4xl;
}

.guarantyText{
    @apply mt-3 pr-10 pb-20 font-semibold;
    @apply text-lg text-gray-900;
}

.functionWrapper{
    @apply mx-2 px-10 pb-10 mb-10 shadow-xl;
    @apply md:shadow-none;
}

.functionIcon{
    @apply w-full flex justify-center;
}

.functionIconBadge{
    @apply px-4 py-1 text-white bg-[#59c2abfb] rounded-2xl -ml-2 -mt-2;
}

.functionTitle{
    @apply mt-16 font-bold text-xl text-center group-hover:text-[#59c2abfb];
}

.functionText{
    @apply mt-3 text-center text-gray-500
}

.carousel{
    @apply  w-full
}

.controls {
    @apply flex justify-center
}

.gap {
    @apply w-2
}

.circleControls {
    @apply bg-gray-300 text-primary-teal hover:bg-primary-teal hover:text-white flex items-center justify-center w-8 h-8 rounded-full
}

.arrow {
    @apply w-5 h-5
}



/*Styling the blogCard*/
.blogCard{
    @apply h-fit w-96 shadow-xl rounded-t-3xl pb-5 lg:mr-5 mt-5;
}
.blogCardImage{
    @apply h-52 pt-5 rounded-t-3xl w-full bg-primary-dark/40 hover:bg-primary-dark/60 bg-blend-color shadow-lg  transition hover:ease-out hover:bg-blend-darken;
    @apply hover:cursor-pointer;
}
.blogCardDate{
    @apply ml-6  w-fit px-4  rounded-3xl bg-primary-white/70 hover:cursor-text;
}
.blogCardDate p{
    @apply text-lg;
}
.blogCardBody{
    @apply p-5;
}
.blogTitle{
    @apply mb-2 text-center text-primary-dark  font-bold;
    @apply hover:cursor-pointer;
}

.blogText{
    @apply text-center ;
}

.blogbottom{
    @apply flex flex-row hover:cursor-pointer ml-3 justify-center items-center w-fit;
}

.blogBookmark{
    @apply text-xl text-primary-white font-bold rounded-full bg-primary-teal w-fit h-fit p-2 mr-2;
}  
/*Second Card styles*/

.cardBox2 {
    @apply w-[23rem] my-4 shadow-lg rounded-3xl
}

.upperSection {
    @apply flex flex-row px-4 py-4
}

.iconDes {
    @apply w-1/5 h-20 bg-[url('../../resources/images/logo01.webp')] bg-cover bg-center
}

.cSection {
    @apply flex flex-col pl-4 w-4/5 items-start
}

.rowItems{
    @apply flex flex-row items-center mt-1
}

.rowDes{
    @apply text-sm ml-2
}

.cTitle {
    @apply font-bold text-sm whitespace-normal
}

.dSection {
    @apply flex flex-row px-4 py-4

}