.buttonDiv {
    @apply hidden;
}

/* Body */
.body {
    @apply w-11/12 h-5/6 gap-32;
    @apply lg:flex mx-auto lg:px-32 lg:py-36 rounded-3xl;
    @apply shadow-xl bg-transparent backdrop-grayscale bg-gradient-to-r from-gray-200 to-green-100;
}

.wrapper {
    @apply mt-[-10rem] w-screen px-48 overflow-hidden py-28;
}

.image {
    @apply scale-75 mt-[-5rem];
}

.cardHeading {
    @apply font-black text-6xl;
}

.text1 {
    @apply text-[#005c81];
}

.text3 {
    @apply mt-2 w-10/12;
}
.text1,
.text2,
.text3 {
    @apply font-bold;
}
.leftSide {
    @apply order-last;
}
