.partnerBody {
    @apply w-full;
    @apply mx-auto my-0 p-2 pb-12;
    @apply lg:h-screen lg:w-11/12;
    @apply lg:mx-auto lg:px-5 lg:mb-20 lg:px-12 lg:pb-24;
}

.heading {
    @apply mx-auto my-4 w-max py-10;
    @apply text-3xl font-black;
    @apply md:text-4xl;
    @apply lg:mx-auto lg:my-5 lg:w-max;
    @apply lg:text-4xl;
}

.partnerLogoDiv {
    @apply h-24;
    @apply md:h-20;
    @apply lg:h-24;
}
.partnerLogo {
    @apply mx-auto my-0 h-full w-8/12;
    @apply scale-75;
    @apply md:scale-90;
    @apply md:h-full md:w-9/12;
    @apply lg:h-4/5 lg:w-10/12;
    @apply md:scale-95 hover:scale-105 ease-out duration-500;
    @apply lg:scale-100 hover:scale-105 ease-out duration-500;
}
.partnerLogo2 {
    @apply h-full mx-auto my-0;
    @apply lg:scale-100 hover:scale-105 ease-out duration-500 cursor-pointer;
}
.partnerLogoEmbodiment {
    @apply grid grid-cols-2 gap-3;
    @apply w-full h-full;
    @apply mx-auto my-5;
    @apply md:grid-cols-4 md:gap-4 md:p-2 md:gap-2 md:w-full md:pb-20;
    @apply lg:grid lg:gap-2 lg:grid-cols-6 lg:p-5 lg:w-9/12 lg:h-3/4;
}

.partnerLogoEmbodiment2 {
    @apply grid grid-cols-2 gap-3;
    @apply w-full h-full;
    @apply mx-auto my-5;
    @apply md:grid-cols-5 md:gap-4 md:p-2 md:gap-2 md:w-full md:pb-20;
    @apply lg:grid lg:gap-2 lg:grid-cols-5 lg:p-5 lg:w-9/12 lg:h-1/4;
}
