.userWrapper {
    @apply p-20;
}

.userTitle {
    @apply text-center font-bold text-4xl;
}

.userStoriesWrapper {
    @apply h-full flex flex-row;
}


.aboutWrapper {
    @apply bg-fixed h-max bg-cover;
}

.aboutWrapper {
    background-image: url(../../resources/images/road.jpeg);
}
    

.aboutOverlay {
    @apply backdrop-brightness-125 bg-gradient-to-r from-violet-900 to-amber-700 p-20 h-full w-full;
    @apply md:py-6 md:px-14;
    @apply lg:py-8 lg:px-32; 
}

.divButton {
    @apply flex justify-center;
}

.singleButton{
    @apply transition ease-in-out delay-150 hover:-translate-y-3  hover:opacity-75 duration-150 inline-block p-3 bg-[#7EBCAD] mb-2 text-white uppercase rounded-full;
}

.textSingleButton{
    @apply text-lg font-semibold text-center text-primary-dark;
}

.secondSectionText{
    @apply flex justify-center text-xl text-white font-bold pt-20;
}

.divButtons{
    @apply flex justify-center mt-5 mb-10 space-x-4 pb-20;
}

.styleButtons{
    @apply transition ease-in-out delay-150 hover:-translate-y-3  hover:opacity-75 duration-150 inline-block p-3 bg-[#7EBCAD] mb-2 text-white uppercase rounded-full;
}
