.wrapper{
    @apply mt-52 mb-32;
}

.heading{
    @apply text-4xl text-center font-bold mb-40;
}

.cards {
    @apply grid grid-cols-1 place-content-center;
    @apply md:grid-cols-2;
    @apply lg:grid-cols-4;
}

.buttons{
    @apply mt-12 w-full mx-auto;
    @apply md:flex md:justify-center;
}

.button{
    @apply px-12 mx-6 py-2 mb-16 rounded-3xl font-bold;
}
