.mainWrapper {
    @apply flex flex-row justify-between items-center px-2 py-2;
    @apply md:px-3;
}

.logoImage {
    @apply h-5 w-full;
    @apply md:h-8;
    @apply xl:h-10;
}

.mobileMenu {
    @apply block w-fit text-3xl mr-3;
    @apply md:mr-4;
    @apply lg:mr-5;
    @apply xl:hidden;
}

.linkIcon {
    @apply text-xl ml-1.5;
}

.mainLinksWrapper {
    @apply flex flex-row justify-between items-center ml-auto mr-5;
}

.mainLinks {
    @apply relative text-[15px] font-bold ml-3.5 hidden flex-row items-center justify-between p-2 py-3;
    @apply xl:flex
}

.mainMenu1 {
    @apply absolute z-10 flex flex-col top-2 w-44 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.subMenu1 {
    @apply absolute ml-12 left-3/4 -top-2 z-20 flex flex-col top-0 w-52 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.subMenu2 {
    @apply absolute ml-12 left-3/4 -top-2 z-20 flex flex-col top-0 w-52 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.mainHover1:hover .mainMenu1 {
    @apply visible h-auto w-44 top-9 opacity-100 shadow-md;
}

.topBorder {
    @apply w-0 h-0.5 duration-500 ease-in-out -mx-2 -mt-0.5;
}

.mainHover1:hover .topBorder {
    @apply w-44 h-0.5;
}

.subHover1:hover .subMenu1 {
    @apply visible h-auto w-52 ml-12 left-3/4 -top-2 opacity-100 shadow-md;
}

.topBorder1 {
    @apply w-0 h-0.5 duration-500 ease-in-out -mx-2 -mt-0.5;
}

.subHover1:hover .topBorder1 {
    @apply w-52 h-0.5;
}

.subHover2:hover .subMenu2 {
    @apply visible h-auto w-52 ml-12 left-3/4 -top-2 opacity-100 shadow-md;
}

.topBorder2 {
    @apply w-0 h-0.5 duration-500 ease-in-out -mx-2 -mt-0.5;
}

.subHover2:hover .topBorder2 {
    @apply w-52 h-0.5;
}

.subLinks {
    @apply relative mx-2 py-2.5 border-b-[0.5px] text-[15px];
}

.accountWrapper {
    @apply flex flex-row justify-between items-center mr-6;
}

.accountIcon {
    @apply mx-1 text-lg;
    @apply sm:text-xl;
    @apply md:text-2xl;
}

.accountLink {
    @apply mx-1 hidden text-opacity-80 font-bold duration-300 ease-in-out;
    @apply lg:flex;
}

.accountOr {
    @apply text-sm hidden text-opacity-80 font-semibold;
    @apply lg:flex;
}

.buttonWrapper {
    @apply flex flex-row justify-between items-center px-4 py-3 rounded-3xl shadow-md drop-shadow-lg;
}

.buttonText {
    @apply font-bold ml-1.5 text-xs whitespace-normal;
    @apply sm:text-sm;
    @apply md:text-base;
}


/* Mobile Menu */

.mobileMainWrapper {
    @apply fixed z-50 top-0 left-0 h-screen w-full duration-[20ms] ease-in-out shadow-md drop-shadow-lg overflow-y-scroll bg-opacity-60;
}

.mobileWrapper {
    @apply fixed z-50 top-0 left-0 h-screen w-80 duration-300 ease-in-out shadow-md drop-shadow-lg overflow-y-scroll;
    @apply md:w-[25rem];
}

.closeMenu {
    @apply sticky top-3 left-full mr-3 h-8 w-8 rounded-full flex justify-center items-center shadow-md drop-shadow-md;
}

.mobileLinksWrapper {
    @apply mt-8;
}

.mobileLinks {
    @apply flex flex-row flex-wrap justify-between items-center border-t-[0.5px] px-6 py-4 font-bold text-sm;
}

.mobileSubLinks {
    @apply transition-all duration-300 ease-in-out;
}

.mobileSubLinksInvisible {
    @apply max-h-0 invisible opacity-0 overflow-hidden;
}

.mobileSubLinksVisible {
    @apply max-h-[60rem] visible opacity-100 overflow-hidden;
}


/* Stripe Menu styles */

.navBar {
    @apply flex flex-row items-center justify-between px-2 py-1 bg-gradient-to-tr;
}

.logoImage1 {
    @apply h-5 z-50 w-full;
    @apply md:h-8;
    @apply xl:h-10;
}

.navMenu {
    @apply relative z-50 flex flex-row flex-wrap items-center justify-between w-[35rem] my-0 ml-auto mr-44;
    transform-style: preserve-3d;
    perspective: 2000px;
}

.navMenuItemNone {
    @apply text-primary-dark font-semibold cursor-pointer grow flex items-center justify-center tracking-wider py-2 px-2 h-20;
    transform-style: preserve-3d;
    perspective: 2000px;
}

.navMenuItem {
    @apply text-primary-dark font-semibold cursor-pointer grow flex items-center justify-center tracking-wider py-2 px-2 h-20;
    transform-style: preserve-3d;
    perspective: 2000px;
}

.navMenuItem:hover ~ .subMenuCon .subMenuWrapper {
    @apply right-auto animate-clipPath opacity-100 transition-all duration-300 ease-in;
}

.navMenuItem:nth-of-type(1):hover ~ .subMenuCon .subMenuWrapper {
    @apply -left-20;
    clip-path: inset(0 28.75em 0 0 round 10px);
    height: 14em;
}

.navMenuItem:nth-of-type(2):hover ~ .subMenuCon .subMenuWrapper {
    @apply left-4 h-[35em];
    clip-path: inset(0 14em 0 0 round 10px);
}

.navMenuItem:nth-of-type(3):hover ~ .subMenuCon .subMenuWrapper {
    @apply left-40 h-[17em];
    clip-path: inset(0 26em 0 0 round 10px);
}

.navMenuItem:nth-of-type(4):hover ~ .subMenuCon .subMenuWrapper {
    @apply left-[315px] h-[40.5em];
    clip-path: inset(0 23.75em 0 0 round 10px);
}


.navMenuItem .subMenu {
    @apply absolute top-[5em] -left-40 rounded-md min-w-[40em] pointer-events-none box-border z-50 opacity-0 transition-all duration-300 ease-in;
    color: rgba(0,0,0,0.5);
    clip-path: inset(0 60em 0em 60em);
}

.navMenuItem .second {
    @apply min-w-[27em];
}

.navMenuItem .third {
    @apply min-w-[29em];
}


.navMenuText {
    @apply text-base text-primary-dark text-center  duration-300 ease-in;
}

.navMenuText::after {
    @apply absolute content-[''] opacity-0 -bottom-4 left-1/2 border-solid border-[9px] pointer-events-none border-transparent  border-b-primary-white duration-300 ease-in;
}

.navMenuItem:hover .navMenuText::after {
    @apply bottom-3 opacity-100 duration-300 ease-in;
}

.navMenuItem:hover .subMenu {
    @apply opacity-100 ml-0;
}

.navMenuItem:hover .subMenu {
    pointer-events: all;
    clip-path: inset(0 0 0 0);
}

.navMenuItem:hover ~ .subMenuCon .subMenuWrapper {
    @apply transition-all duration-300 ease-in;
}

.navMenuItem .iconBox * {
    transition: all 0.1s ease-out;
    position: relative;
}

.navMenuItem .iconBox {
    @apply cursor-pointer w-full m-0 h-auto grid grid-cols-3 gap-10;
    row-gap: 0;
    line-height: 1em;
}

.subMenu {
    @apply text-base;
}


.subMenuWrapper {
    @apply absolute pointer-events-none font-normal opacity-0 scale-100 translate-y-20 -rotate-[18deg];
    @apply  origin-center box-border top-[3em] rounded-md block h-full w-[56rem] duration-300 ease-out;
}    

.subMenuCon {
    @apply absolute -z-10 w-full min-w-full -top-1 shadow-md drop-shadow-lg;
}

