.subMenuTitle {
    @apply text-xl font-bold underline my-3;
}

.linkWrapper {
    @apply flex flex-row items-center justify-start my-2;
}

.linkIcon {
    @apply text-lg mr-2.5 h-5 w-5;
}

.linkTitle {
    @apply text-base;
}

.linkSubTitle {
    @apply text-xs;
}

