.ProvidersPageTopContainer {
    @apply w-screen h-4/6;
    @apply px-10 py-24;
    @apply md:px-20;
    @apply lg:pl-80 lg:py-28 lg:pb-12;
    @apply bg-no-repeat bg-cover bg-blend-darken;
    background-image: url("../../resources/images/blue-bg1.jpg");
}
.ProvidersPageTopBody {
    @apply h-full;
    @apply flex flex-col;
    @apply md:w-4/5;
    @apply lg:w-3/5 lg:h-4/5;
}

.top {
    @apply lg:h-1/3;
}
.bottom {
    @apply h-full space-y-52;
    @apply lg:h-1/3 lg:space-y-64;
}
/* .textDiv{
    @apply ;
} */
.text {
    @apply w-full h-10 max-h-full;
    @apply font-black text-3xl text-[#f3f3f3] overflow-hidden;
    @apply md:text-6xl;
    @apply md:h-16;
    @apply lg:text-6xl;
    @apply lg:h-16;
}

.bottomText {
    @apply text-sm text-[#e6e6e6] font-bold mt-8;
    @apply text-xl mt-4;
    @apply lg:text-xl lg:mt-8;
}
.span1 {
    @apply absolute inset-0;
    @apply border-0 group-hover:border-[25px] border-white rounded-full;
    @apply ease-linear duration-100 transition-all;
    @apply hover:text-black;
}
.span2 {
    @apply relative w-full;
    @apply text-center text-white;
    @apply transition-colors duration-200 ease-in-out;
}
.link {
    @apply  w-28 md:w-36 lg:w-44;
    @apply relative inline-flex items-center justify-start inline-block overflow-hidden;
    @apply px-1 lg:px-3 py-3  font-medium  opacity-75;
    @apply  transition-all bg-[#63ae9c] rounded-full hover:opacity-100;
}

.link2 {
    @apply bg-[#005c81];
}

.buttons {
    @apply flex space-x-8 mt-16;
}
