.blogSection{
    @apply py-20;
}
.blogHeader{
    @apply text-4xl text-center mb-20 font-bold;
}

.blogSectionMain{
    @apply flex flex-col lg:flex-row lg:justify-center items-center m-auto flex-wrap;
}

.containerBtn{
    @apply flex flex-row flex-wrap m-auto justify-center mt-32;
}

.nextLinkClassName{
    @apply w-fit h-20  rounded-full bg-primary-white text-primary-dark hover:bg-primary-teal hover:text-primary-white;
}
.nextButton{
    @apply w-32;
}

.previoustLinkClassName{
    @apply w-fit h-fit  rounded-3xl bg-primary-white text-primary-dark hover:bg-primary-teal hover:text-primary-white;
}

.containerBtn a{
    @apply p-3 w-3 h-3 shadow-sm rounded-full mr-5 bg-primary-white text-primary-dark hover:bg-primary-teal hover:text-primary-white focus:bg-primary-teal focus:text-primary-white;
}