.tabTitle{
    @apply px-4 py-2 font-semibold text-gray-400 text-base;
}

.tabTitleSelected{
    @apply px-4 py-2 font-semibold text-sky-700 text-base;
}

.pageHeading{
    @apply text-center text-5xl md:text-[60px] text-gray-900 font-bold;
}

.pageSubtitle{
    @apply text-center text-gray-600 font-semibold text-lg my-4;
}

